  transition: all 0.5s;
  z-index: 997;
  background: rgb(224, 218, 218);
}

.header.header-scrolled {
  background: rgba(255, 255, 255, 1);
  /* padding: 20px 0 15px 0; */
  height: 80px;
  transition: all 0.5s;
  -webkit-box-shadow: 0px 3px 5px -2px rgb(0 0 0 / 37%);
  -moz-box-shadow: 0px 3px 5px -2px rgba(0,0,0,0.37);
